<template>
  <div>
    <h3>Afficher seulement:</h3>
    <div classs="checks">
      <ul v-for="typ in types" v-bind:key="typ.id">
        <sorting-item :type="typ"></sorting-item>
      </ul>
    </div>
  </div>
</template>

<script>
import types from "./../../utility/types.JSON";
import SortingItem from "./sorting-item.vue";

export default {
  data() {
    return {
      selectedTypes: [],
      types: types.types,
    };
  },
  methods: {
    addType(type) {
      this.types.find((data) => data.name == type.name).state = true;
      this.selectedTypes.push(type.name);
      this.$root.$refs.domainList.updateTypes(this.selectedTypes);
    },
    removeType(type) {
      this.types.find((data) => data.name == type.name).state = false;
      this.selectedTypes = this.selectedTypes.filter(
        (data) => data != type.name
      );
      this.$root.$refs.domainList.updateTypes(this.selectedTypes);
    },
  },
  components: {
    SortingItem,
  },
  created() {
    this.$root.$refs.sortingList = this;
  },
};
</script>

<style>
h3 {
  margin-top: 40px;
}
select {
  width: 50%;
  height: auto;
  border: 1px solid var(--primary);
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 5px;
}
ul {
  list-style: none;
  padding: 10px;
}
.checks {
  margin-top: 20px;
}
</style>
