<template>
  <div class="conditionContainer">
    <h3>Conditions de travail :</h3>
    <ul v-for="condition in conditions" v-bind:key="condition.id">
      <condition-item :condition="condition"></condition-item>
    </ul>
  </div>
</template>

<script>
import conditions from "./../..//utility/conditions.JSON";
import ConditionItem from "./condition-item.vue";

export default {
  data() {
    return {
      conditions: conditions.conditions,
      selectedConditions: [],
    };
  },
  components: {
    ConditionItem,
  },
  methods: {
    addCondition(condition) {
      this.conditions.find((data) => data.id == condition.id).state = true;
      this.selectedConditions.push(condition.id);
      this.$root.$refs.domainList.updateConditions(this.selectedConditions);
    },
    removeCondition(condition) {
      this.conditions.find((data) => data.id == condition.id).state = false;
      this.selectedConditions = this.selectedConditions.filter(
        (data) => data != condition.id
      );
      this.$root.$refs.domainList.updateConditions(this.selectedConditions);
    },
  },
  created() {
    this.$root.$refs.conditionList = this;
    this.selectedConditions = this.$root.$refs.domainList.conditions;
  },
};
</script>

<style scoped>
h3 {
  padding: 15px 0px;
  margin-top: 40px;
}
</style>
